.main-title {
  margin: 0;
  padding: 0;
  width: 320px;
}

.main-title__text {
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 30px;
  color: #283645;
  text-align: center;
  margin: 0;
  padding: 0;
  padding-top: 50px;
  padding-bottom: 55px;
}

.main-title__container {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 660px) {

  .main-title {
    width: 660px;
  }

  .main-title__container {
    width: 620px;
  }

  .main-title__text {
    font-size: 18px;
    line-height: 30px;
  }

}

@media (min-width: 1200px) {

  .main-title {
    width: 1200px;
  }

  .main-title__container {
    width: 940px;
    padding: 0 130px;
  }

}
